  text-align: center; }

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  margin: 0;
  background-image: linear-gradient(to top, #000 0%, #c04d00 30%, #fff 50%, #c04d00 80%, #000 102%);
  background-attachment: fixed; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-size: 1.6rem;
  margin: 0; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
h2,
h3 {
  font-weight: 900;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

.todo {
  height: 100%; }

.contenedor__inicio {
  position: fixed;
  left: auto;
  right: auto;
  top: 40%;
  color: #fff;
  margin: 0 auto 0 auto;
  width: 100%; }
  .contenedor__inicio button {
    width: auto;
    margin: 0 auto 0 auto;
    font-size: 2.5rem;
    color: #fff;
    text-align: center;
    display: block;
    background-color: #c04d00;
    border: none;
    border-radius: 1.2rem;
    height: 4rem; }
  .contenedor__inicio h1 {
    color: #000;
    width: auto;
    margin: 0 auto 0 auto; }
  .contenedor__inicio .beneficios {
    font-size: 2.5rem;
    color: #999;
    margin: .1rem;
    text-align: center; }
  .contenedor__inicio .cargando {
    text-align: center;
    color: #333;
    font-size: 1.8rem; }
  .contenedor__inicio .oculto {
    display: none; }

.todo2 {
  height: 100%;
  background-image: linear-gradient(to top, #000, #c04d00 30%, #000 80%); }

.contenedor_mayor {
  position: fixed;
  margin: 0 auto 0 auto;
  top: 14%;
  width: 100%; }

.contenedor_ingresar {
  width: 70%;
  text-align: center; }
  .contenedor_ingresar h1 {
    color: #999;
    font-size: 2rem; }
  .contenedor_ingresar .botones {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
  .contenedor_ingresar a {
    text-decoration: none;
    background-color: #000;
    color: #fff;
    width: 100%;
    height: 4rem;
    margin-bottom: 1rem;
    text-align: center center;
    padding: 3rem auto 3rem auto;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: rem;
    text-transform: uppercase;
    font-weight: bold; }
  .contenedor_ingresar img {
    margin-top: 3rem;
    width: 57%;
    border-radius: 50%; }

.contenedor_principal {
  height: 100%;
  padding-top: 3rem;
  align-items: center;
  align-self: center; }
  .contenedor_principal h1 {
    display: none; }

.panel-superior {
  align-items: center;
  align-self: center;
  display: grid;
  grid-template-columns: 15% 60% 25%;
  width: 90%;
  margin: 0 auto 0 auto;
  max-height: 5rem; }
  @media (min-width: 550px) {
    .panel-superior {
      width: 60%; } }
  .panel-superior .logo_inicio {
    width: 70%;
    border-radius: 50%;
    grid-column: 1/2;
    margin: auto .2rem auto .2rem;
    max-height: 5rem; }
    @media (min-width: 550px) {
      .panel-superior .logo_inicio {
        width: auto; } }
  .panel-superior form {
    margin: auto;
    width: 100%;
    grid-column: 2/3;
    height: 90%;
    max-height: 5rem; }
    .panel-superior form fieldset {
      border: none; }
    .panel-superior form input {
      background-color: #999;
      height: 3.2rem;
      width: 100%;
      border-radius: .6rem; }
  .panel-superior .logotipo_inicio {
    grid-column: 3/4; }

.contenedor_logotipo {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: auto .5rem auto .5rem;
  height: 50%; }

.logotipo_inicio img {
  width: 90%;
  max-height: 5rem; }
  @media (min-width: 550px) {
    .logotipo_inicio img {
      width: auto; } }

.galeria {
  width: 95%;
  background-image: url("../img/galeria1.webp");
  background-size: cover;
  background-position: center center;
  margin: 3rem auto 0 auto;
  height: 13rem;
  border-radius: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center; }

@media (min-width: 768px) {
  .galeria {
    height: 18rem; } }

@media (min-width: 1024px) {
  .galeria {
    height: 23rem; } }

.anterior,
.siguiente {
  color: #999;
  margin: 0 1.5rem 0 1.5rem;
  font-size: 3rem;
  font-weight: bold; }
  @media (min-width: 480px) {
    .anterior,
    .siguiente {
      font-size: 5rem;
      margin: 0 3rem 0 3rem; } }

.oculto {
  visibility: hidden; }

.oculto2 {
  visibility: hidden; }

.galeria-foto {
  position: fixed;
  height: 20rem;
  width: 90%;
  top: 30%;
  left: 5%;
  border: .5rem solid black; }

.cruz {
  position: fixed;
  top: calc(30% + 18rem);
  width: 20%;
  left: 40%;
  z-index: 1;
  font-size: 5rem;
  color: #fff;
  background-color: #999;
  text-align: center;
  border-radius: 2rem;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; }

.navegacion {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 3rem;
  margin-top: .3rem;
  background-color: #8d3900; }
  .navegacion .nav {
    display: flex;
    flex: 1 1 calc(30% - 1rem);
    text-align: center;
    justify-content: center;
    align-items: center;
    height: 100%;
    border-radius: 1.2rem; }
    .navegacion .nav a {
      color: #fff; }
      .navegacion .nav a:visited {
        color: #fff; }
    .navegacion .nav:focus {
      background-color: #c04d00; }
    .navegacion .nav:hover {
      background-color: #c04d00; }

.contenedor-productos {
  width: 90%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 2rem auto 2rem auto;
  column-gap: 3rem;
  row-gap: 3rem; }

@media (min-width: 768px) {
  .contenedor-productos {
    grid-template-columns: repeat(3, 1fr); } }

.producto {
  background-color: rgba(243, 98, 0, 0.3);
  border: solid 0.3rem #333;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .producto p {
    text-transform: uppercase;
    font-weight: bold;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: negro; }

.seccion-productos h2 {
  font-size: 4rem;
  color: #333; }
